


































import { Component, Vue } from "vue-property-decorator";

@Component({})
export default class LoginPage extends Vue {
  private error = false;
  private UserData = {
    login: "",
    pass: "",
  };

  private onLogin() {
    this.error = false;
    if (this.UserData.login.length >= 6 && this.UserData.pass.length >= 6) {
      this.$router.push({ name: "home" });
    } else {
      this.error = true;
    }
  }
}
